import React, { useState, useEffect } from 'react';

const WhoAmIGame = ({ participants }) => {
  const [currentRound, setCurrentRound] = useState(0);
  const [score, setScore] = useState(0);
  const [selectedAnswer, setSelectedAnswer] = useState(null);
  const [gameOver, setGameOver] = useState(false);
  const [difficulty, setDifficulty] = useState(5); // Default to medium

  // Difficulty blur mapping
  const blurLevels = {
    0: '0px',   // Easy
    5: '5px',   // Medium
    8: '8px'    // Hard
  };

  // Shuffle participants and create game rounds
  const [gameRounds, setGameRounds] = useState([]);

  useEffect(() => {
    // Shuffle participants and create game rounds
    const shuffledParticipants = [...participants].sort(() => 0.5 - Math.random());
    const rounds = shuffledParticipants.map(correctPerson => {
      // Create options including the correct answer
      const otherOptions = shuffledParticipants
        .filter(p => p.name !== correctPerson.name)
        .sort(() => 0.5 - Math.random())
        .slice(0, 3);
      
      const options = [
        ...otherOptions,
        correctPerson
      ].sort(() => 0.5 - Math.random());

      return {
        correctPerson,
        options
      };
    });

    setGameRounds(rounds);
  }, [participants]);

  const handleAnswer = (selectedPerson) => {
    const isCorrect = selectedPerson.name === gameRounds[currentRound].correctPerson.name;
    
    if (isCorrect) {
      setScore(prev => prev + 1);
    }

    setSelectedAnswer(selectedPerson);

    // Move to next round after a short delay
    setTimeout(() => {
      if (currentRound < gameRounds.length - 1) {
        setCurrentRound(prev => prev + 1);
        setSelectedAnswer(null);
      } else {
        setGameOver(true);
      }
    }, 2500);
  };

  const resetGame = () => {
    setCurrentRound(0);
    setScore(0);
    setSelectedAnswer(null);
    setGameOver(false);
    
    // Reshuffle participants
    const shuffledParticipants = [...participants].sort(() => 0.5 - Math.random());
    const rounds = shuffledParticipants.map(correctPerson => {
      const otherOptions = shuffledParticipants
        .filter(p => p.name !== correctPerson.name)
        .sort(() => 0.5 - Math.random())
        .slice(0, 3);
      
      const options = [
        ...otherOptions,
        correctPerson
      ].sort(() => 0.5 - Math.random());

      return {
        correctPerson,
        options
      };
    });

    setGameRounds(rounds);
  };

  // Difficulty selection component
  const DifficultySelector = () => (
    <div style={{
      display: 'flex',
      justifyContent: 'center',
      gap: '20px',
      marginBottom: '20px'
    }}>
      {Object.entries(blurLevels).map(([level, blur]) => (
        <button
          key={level}
          onClick={() => setDifficulty(Number(level))}
          style={{
            padding: '10px 20px',
            backgroundColor: difficulty === Number(level) ? '#4CAF50' : '#3498db',
            color: 'white',
            border: 'none',
            borderRadius: '5px',
            cursor: 'pointer'
          }}
        >
          {level === '0' ? 'Easy' : level === '5' ? 'Medium' : 'Hard'}
        </button>
      ))}
    </div>
  );

if (gameOver) {
  return (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      height: '100vh',
      backgroundColor: '#f0f8ff',
      textAlign: 'center',
      padding: '20px'
    }}>
      <h1>Game Over!</h1>
      <div style={{
        fontSize: '2rem',
        margin: '20px 0'
      }}>
        Your Final Score: {score} out of {gameRounds.length}
      </div>
      <video 
        style={{
          width: '800px',
          maxWidth: '90%',
          borderRadius: '15px',
          marginBottom: '20px'
        }}
        controls 
        autoPlay
        onEnded={() => setCurrentGame('menu')}
      >
        <source 
          src={process.env.PUBLIC_URL + '/cat.mp4'} 
          type="video/mp4" 
        />
        Your browser does not support the video tag.
      </video>
      <button 
        onClick={() => setCurrentGame('menu')}
        style={{
          padding: '15px 30px',
          fontSize: '1.5rem',
          backgroundColor: '#4CAF50',
          color: 'white',
          border: 'none',
          borderRadius: '10px',
          cursor: 'pointer'
        }}
      >
        Back to Menu
      </button>
    </div>
  );
}

  const currentRoundData = gameRounds[currentRound] || { correctPerson: {}, options: [] };

  return (
    <div style={{
      display: 'flex',
      flexDirection: 'column',
      alignItems: 'center',
      justifyContent: 'center',
      height: '100vh',
      backgroundColor: '#f0f8ff',
      padding: '20px'
    }}>
      <h1>Who Am I?</h1>
      
      {/* Difficulty Selector */}
      <DifficultySelector />
      
      <div style={{
        fontSize: '1.5rem',
        marginBottom: '20px'
      }}>
        Score: {score} | Round {currentRound + 1} of {gameRounds.length}
      </div>

	  <img 
	    src={currentRoundData.correctPerson.imageUrl} 
	    alt="Participant image"
	    style={{
 	    width: '100%',      // Full width of container
 	    maxWidth: '800px', // Maximum width
 	    height: 'auto',     // Maintain aspect ratio
	    minHeight: '300px', // Minimum height
 	    borderRadius: '20px', 
	    marginBottom: '30px',
        filter: `blur(${blurLevels[difficulty]})`,
        objectFit: 'cover', 
     	boxShadow: '0 10px 20px rgba(0,0,0,0.2)',
    	display: 'block',   
    	margin: '0 auto'    
	   }}
	 />

      <div style={{
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'center',
        gap: '20px'
      }}>
        {currentRoundData.options.map((person, index) => (
          <button
            key={index}
            onClick={() => handleAnswer(person)}
            disabled={!!selectedAnswer}
			style={{
 			 padding: '15px 30px',
  			fontSize: '1.5rem',
 			 backgroundColor: selectedAnswer 
 			   ? (selectedAnswer.name === person.name 
  			      ? (selectedAnswer.name === currentRoundData.correctPerson.name 
    		        ? '#4CAF50'  // Correct answer - green
   			         : '#FF6B6B') // Wrong answer - red
   			     : (person.name === currentRoundData.correctPerson.name 
 			           ? '#2ecc71'  // Highlight correct answer in green
			            : '#3498db')) // Unselected options - blue
 			   : '#3498db',        // Default color before selection
 			 color: 'white',
			  border: 'none',
			  borderRadius: '10px',
			  cursor: 'pointer', // Simplified cursor
			  transition: 'transform 0.2s',
			  opacity: selectedAnswer 
			    ? (selectedAnswer.name === person.name || person.name === currentRoundData.correctPerson.name 
			       ? 1 
  			      : 0.5) 
 			   : 1
			}}
          >
            {person.name}
          </button>
        ))}
      </div>
    </div>
  );
};

